<template>
  <div>
    
    <svg id = 'sideGraph'
    @click = 'log()'
    :height = 'sideSvgConfig.height' :width = 'sideSvgConfig.width'
    position = 'absolute' left = '500'
    top = '20'
    background-color = 'black'>
    <g id = 'recomUser' v-if = 'recomFlag'>
      
      <rect
      :x = 'recomArr[5].x'
      :y = 'recomArr[0].y'
      :width = 'recomArr[5].width'
      :height = 'sideSvgConfig.height *0.15'
      fill = '#affcb2'
      rx = 5>
        
      </rect>
      <image xlink:href = 'netease.png'
      :x = 'recomArr[5].x -10'
      :y = 'recomArr[0].y+10'
      :width = 'sideSvgConfig.height *0.12'>
      </image>
      <text 
      text-anchor = 'end'
      font-size = 22
      font-family = 'sans-serif'
      font-weight = 'bold'
      :dx = 'recomArr[5].x + recomArr[5].width -15'
      :dy = 'recomArr[0].y+50'>
      @{{recomName.length>5? recomName.substring(0,5) + '...' :recomName}}
      </text>
      <text
      text-anchor = 'end'
      font-size = 14
      font-family = 'sans-serif'
      :dx = 'recomArr[5].x + recomArr[5].width -15'
      :dy = 'recomArr[0].y + 80'>
      ID:{{recomID}}
      </text>
    </g>
    <g id = 'recomMusic' v-if = 'recomFlag' >
      <g v-for = '(d,i) in recomArr'
      :key = 'i'
      @mousemove = 'hover(i)'
      @mouseout = 'hoverFlag = false'
      
      >
      <rect
      :x = 'd.x'
      :y = 'd.y'
      :height = 'd.height'
      :width = 'd.width'
      :fill = 'd.color2'
      
      rx = 5>
        
      </rect>
      <rect
      :x="d.x + d.width/2 - d.height/3-5" :y="d.y+35"
      :height="d.height*2/3+10" :width="d.height*2/3+10"
      fill = 'white'
      
      >
        
      </rect>
      <image v-bind:xlink:href="musicData[recomList[i].musicID].img?musicData[recomList[i].musicID].img:'netease.png'" 
      :x="d.x + d.width/2 - d.height/3" :y="d.y+40" 
      :height="d.height*2/3" :width="d.height*2/3"
      preserveAspectRatio="none meet"
       />
       <rect
       :x = 'd.x'
       :y = 'd.y'
       :height = '30'
       :width = 'd.width'
       :fill = 'd.color'
       rx = 5>
       </rect>
       <a :href = "musicData[recomList[i].musicID].href" target = '_blank' pointer = 'cursor'>
         <image xlink:href = 'jump.png' width = 20 :x = 'd.x + d.width - 25' :y = 'd.y+5'>
           
         </image>
       </a>
        
        <text :dx = 'd.x + 10'
        :dy = 'd.y + 20'
        font-size = 14
        font-color = 'black'
        font-family = 'sans-serif'
        >No.{{i+1}}
        </text>
        
        <text :dx = 'd.x + d.width - 30'
        text-anchor = 'end'
        :dy = 'd.y + 20'
        font-size = 14
        font-color = 'black'
        font-family = 'sans-serif'
        >{{musicData[recomList[i].musicID].name.substring(0,15)}}
        </text>
      </g>
    </g>
    <g id = 'tip' v-if = 'hoverFlag'
    :transform = "'translate('+[hoverXY[0]+10,hoverXY[1]+10]+')'">
      <rect 
      x = 0
      y = -5
      fill = '#000000'
      height = 200
      width = 150
      rx = 10
      fill-opacity = 0.8
      >
      </rect>
      <text dx = 10 dy = 15 fill = 'white' font-size = 12>歌曲名称:</text>
      
      <text
      text-anchor = 'end'
      dx = 140
      dy = 30
      fill = 'white'
      font-size = 12
      >
      {{musicData[stressedMusic].name.substring(0,15)}}
       
      </text>
      <text dx = 10 dy = 45 fill = 'white' font-size = 12>歌曲ID:</text>
      <text
      text-anchor = 'end'
      dx = 140
      dy = 60
      fill = 'white'
      font-size = 12
      >
       {{stressedMusic.substring(0,15)}}
      </text>
      
      <text dx = 10 dy = 75 fill = 'white' font-size = 12>歌手:</text>
      <text
      text-anchor = 'end'
      dx = 140
      dy = 90
      fill = 'white'
      font-size = 12
      >
       {{musicData[stressedMusic].singer.substring(0,15)}}
      </text>
      <text dx = 10 dy = 105 fill = 'white' font-size = 12>专辑:</text>
      <text
      text-anchor = 'end'
      dx = 140
      dy = 120
      fill = 'white'
      font-size = 12
      >
        {{musicData[stressedMusic].album.substring(0,15)}}
      </text>
      <text dx = 10 dy = 135 fill = 'white' font-size = 12>编曲:</text>
      <text
      text-anchor = 'end'
      dx = 140
      dy = 150
      fill = 'white'
      font-size = 12
      >
       {{musicData[stressedMusic].composer.substring(0,15)}}
      </text>
      <text dx = 10 dy = 165 fill = 'white' font-size = 12>作者:</text>
      <text
      text-anchor = 'end'
      dx = 140
      dy = 180
      fill = 'white'
      font-size = 12
      >
       {{musicData[stressedMusic].author.substring(0,15)}}
      </text>
      
      
    </g>
    </svg>
  </div>
  
</template>

<script>
import * as d3 from "d3"
export default {
  name: 'sideView',
  data(){
    let h = this.$store.state.height-20-20+50,
    w = this.$store.state.width/2-5,
    padX = w*0.1/4,
    padY = h*0.1/5
    return {
      sideSvgConfig:{
        height:h,
        width:w,
        backgroundColor:"black",
        opacity:0.8,
        },
        hoverFlag:false,
        hoverXY:[],
        //recomFlag:false,
        stressedMusic:'',
        musicData :{},
        mergeData:{},
        mergeData2:{},
        mergeData3:{},
        
       
        recomArr:[
          {x:padX,y:padY,height:h*0.4,width:w*0.3,color:'#e8e200',color2:'#dde8ae',},
          {x:padX*2+0.3*w,y:padY*2+h*0.15,height:h*0.35,width:w*0.3,color:'#c0c2c8',color2:'#e6e8ef'},
          {x:padX*3+0.6*w,y:padY,height:h*0.25,width:w*0.3,color:'#f2997d',color2:'#ecc59c'},
          {x:padX,y:padY*2+h*0.4,height:h*0.25,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          {x:padX,y:padY*3+h*0.65,height:h*0.25,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          {x:padX*2+w*0.3,y:padY*3+h*0.5,height:h*0.2,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          {x:padX*2+w*0.3,y:padY*4+h*0.7,height:h*0.2,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          {x:padX*3+w*0.6,y:padY*2+h*0.25,height:h*0.21,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          {x:padX*3+w*0.6,y:padY*3+h*0.46,height:h*0.21,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          {x:padX*3+w*0.6,y:padY*4+h*0.67,height:h*0.21,width:w*0.3,color:'#28d4eb',color2:'#9ce4e8'},
          
        ]
      
    }
  },
  mounted(){
    d3.json('music.json',d=>{
      this.musicData = d
    })
    d3.json('data_merge.json',d=>{
      this.mergeData = d
      
    })
    /* d3.json('recomAl2.json',d=>{
      this.mergeData2 = d
      
    }) */
    d3.json('itemCF10.json',d=>{
      this.mergeData3 = d
    })
  },
  computed:{
      algo(){
        return this.$store.state.algo
      },
      recomList(){
        if(this.algo == 'BPR'){
          return this.mergeData[this.recomID]
        }
        else{
          return this.mergeData3[this.recomID]
        }
        
      },
      recomID(){
        return this.$store.state.recomID
      },
      recomName(){
        return this.$store.state.recomName
      },
      recomFlag(){
        if(this.recomID){
          return true
        }
        else{
          return false
        }
      }
  },
  methods:{
    log(){
      this.recomFlag = true
    },
    hover(i){
      this.stressedMusic = this.recomList[i].musicID.toString()
      let x = event.offsetX>this.sideSvgConfig.width - 150? event.offsetX - 170:event.offsetX
      let y = event.offsetY>this.sideSvgConfig.height - 200? event.offsetY - 220:event.offsetY
      this.hoverFlag = true;
      this.hoverXY = [x,y]
    },
  },
  watch:{
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #sideGraph{
    position:absolute;
    left:50%;
    top:5px;
    background-color: black;
    opacity:0.8;
    border-radius: 10px;
    
  }
  
</style>
