<template>
  <div >
    <div id = 'console' :style = 'consoleStyle'>
      <text class = 'text'>User ID:</text>
      <input  class = 'textIn' type = 'text' v-model = 'inputID' placeholder="id">
      
      <button @click = 'input()'  id = 'timeBtn'>Set</button>
      <text class = 'text'>Algorithm:</text>
      <select v-model="selectedAl" class = 'selectIn' >
       <option v-for="(d,i) in options" 
       
       :key="i"
       :value="d" >
         {{ d }}
       </option>
      </select>
      
    </div>
    
    
    <svg id = 'mainGraph' @click = 'log()'
    :height = 'mainSvgConfig.height' :width = 'mainSvgConfig.width'>
    
        <g v-if = 'showData.length >0'>
          <g id = 'linksLeft' v-for = '(d,i) in showData[2]'
           :key = 'i'>
            <line
            :x1 = "getNewPos(d.source,d.target,15)[0]? getNewPos(d.source,d.target,20)[0]:mainSvgConfig.width/2"
            :x2 = 'getNewPos(d.target,d.source,20)[0]? getNewPos(d.target,d.source,25)[0]:mainSvgConfig.width/2'
            :y1 = 'getNewPos(d.source,d.target,15)[1]? getNewPos(d.source,d.target,20)[1]:mainSvgConfig.height/2'
            :y2 = 'getNewPos(d.target,d.source,20)[1]? getNewPos(d.target,d.source,25)[1]:mainSvgConfig.height/2'
            :stroke = 'linksColor(d)'
            :stroke-width = 'linksWidth(d)'
            :stroke-opacity = 'linksOpacity(d)'>
              
            </line>
            <polygon v-if = 'getNewPos(d.source,d.target,15)[0]'
            :points = 'getPoint(d.source,d.target)'
            :fill = 'linksColor(d)'
            :fill-opacity = 'linksOpacity(d)'>
            </polygon>
          </g>
          
        </g>
        <g v-if = 'stressedID && stressedID != showID'>
          <g v-for = '(d,i) in stressedData[1]'
           :key = 'i'>
            <line
            :x1 = "getNewPos(d.source,d.target,15)[0]? getNewPos(d.source,d.target,20)[0]:mainSvgConfig.width/2"
            :x2 = 'getNewPos(d.target,d.source,20)[0]? getNewPos(d.target,d.source,25)[0]:mainSvgConfig.width/2'
            :y1 = 'getNewPos(d.source,d.target,15)[1]? getNewPos(d.source,d.target,20)[1]:mainSvgConfig.height/2'
            :y2 = 'getNewPos(d.target,d.source,20)[1]? getNewPos(d.target,d.source,25)[1]:mainSvgConfig.height/2'
            stroke = '#f2b0c4'
            stroke-width = 2
            stroke-opacity = 1>
            </line>
            <polygon v-if = 'getNewPos(d.source,d.target,15)[0]'
            :points = 'getPoint(d.source,d.target)'
            fill = '#f2b0c4'
            fill-opacity = 1>
            </polygon>
          </g>
        </g>
        <g id = 'circlesLeft'>
          
        </g>
        <g id = 'tip' v-if = 'hoverFlag'
        :transform = "'translate('+[hoverXY[0]+10,hoverXY[1]+10]+')'">
          <rect 
          x = 0
          y = -5
          fill = '#9fd5db'
          height = 200
          width = 150
          rx = 10
          fill-opacity = 0.8
          >
          </rect>
          <text dx = 10 dy = 15 fill = 'black' font-size = 12>用户名:</text>
          
          <text
          text-anchor = 'end'
          dx = 140
          dy = 30
          fill = 'black'
          font-size = 12
          >
            {{user[diction[stressedID]]['用户名']}}
          </text>
          <text dx = 10 dy = 45 fill = 'black' font-size = 12>用户ID:</text>
          <text
          text-anchor = 'end'
          dx = 140
          dy = 60
          fill = 'black'
          font-size = 12
          >
            {{user[diction[stressedID]]['用户ID']}}
          </text>
          
          <text dx = 10 dy = 75 fill = 'black' font-size = 12>性别:</text>
          <text
          text-anchor = 'end'
          dx = 140
          dy = 90
          fill = 'black'
          font-size = 12
          >
            {{user[diction[stressedID]]['性别']}}
          </text>
          <text dx = 10 dy = 105 fill = 'black' font-size = 12>用户等级:</text>
          <text
          text-anchor = 'end'
          dx = 140
          dy = 120
          fill = 'black'
          font-size = 12
          >
            {{user[diction[stressedID]]['用户等级']}}
          </text>
          <text dx = 10 dy = 135 fill = 'black' font-size = 12>关注数:</text>
          <text
          text-anchor = 'end'
          dx = 140
          dy = 150
          fill = 'black'
          font-size = 12
          >
            {{user[diction[stressedID]]['关注数']}}
          </text>
          <text dx = 10 dy = 165 fill = 'black' font-size = 12>粉丝数:</text>
          <text
          text-anchor = 'end'
          dx = 140
          dy = 180
          fill = 'black'
          font-size = 12
          >
            {{user[diction[stressedID]]['粉丝数']}}
          </text>
          
          
        </g>
      
    </svg>
    
    
  </div>
  
</template>

<script>
import * as d3 from "d3"
export default {
  name: 'mainView',
  data(){
    return {
      color:d3.scale.category10(),
      mainSvgConfig:{
        height:this.$store.state.height-20,
        width:this.$store.state.width/2-5,
        backgroundColor:"black",
        opacity:0.8,
      },
      options:['BPR','ITEMCF'],
      selected:'',
      nodes:[],
      links1:[],
      links2:[],
      diction:{},
      showID:'',
      inputID:'',
      stressedID:'',
      user:[],
      hoverFlag:false,
      hoverXY:[],
      selectedAl:'BPR'
    }
  },
  mounted(){
    d3.json('nodes.json',d=>{
      this.nodes = d
    })
    d3.json('links1.json',d=>{
      this.links1 = d
    })
    d3.json('links2.json',d=>{
      this.links2 = d
    })
    d3.json('diction.json',d=>{
      this.diction = d
    })
    d3.csv("user.csv",d=>{
      this.user = d
      console.log(d)
    })
  },
  computed:{
    recomID(){
      return this.$store.state.recomID
    },
    consoleStyle(){
      return "width:"+this.mainSvgConfig.width+"px;height:40px;border-radius:5px;border:0px solid black;"
    },
      force(){
        let nodeSize = this.showData[1].length
        let gravity = nodeSize<30?0.1:nodeSize<100?0.35:nodeSize<200?0.45:nodeSize<500?0.65:0.95
        let charge = nodeSize<30?-1000:nodeSize<100?-900:nodeSize<200?-700:nodeSize<500?-500:-200
        return d3.layout.force()
          .size([this.mainSvgConfig.width,this.mainSvgConfig.height])
          .on("tick",this.tick)
          .gravity(gravity)
          .charge(charge)
          .friction(0.85)
      },
    showIndex(){
      return this.diction[this.showID]
    },
    stressedData(){
      let nds = [this.showID,this.stressedID]
      let lks = []
      for(let i = 0;i<this.showData[2].length;i++){
        if(this.showData[2][i].target.id == this.stressedID){
          nds.push(this.showData[2][i].source.id)
        }
        else if(this.showData[2][i].source.id == this.stressedID){
          nds.push(this.showData[2][i].target.id)
        }
      }
      for(let i = 0;i<this.showData[0].length;i++){
        if((this.showData[0][i].source.id == this.showID && nds.indexOf(this.showData[0][i].target.id) != -1 )||
        (this.showData[0][i].target.id == this.showID && nds.indexOf(this.showData[0][i].source.id) != -1 )){
          lks.push(this.showData[0][i])
        }
      }
      return [nds,lks]
      
    },
    showData(){
      let links = []
      let rela = []
      let indexs = [this.showIndex]
      for(let i = 0;i<this.links1.length;i++){
        if(this.links1[i]['source'] == this.showIndex){
          let edge = {
            source:this.nodes[this.showIndex],
            target:this.nodes[this.links1[i]['target']]
          }
          indexs.push(this.links1[i]['target'])
          links.push(edge)
        }
      }
      for(let i = 0;i<this.links2.length;i++){
        if(this.links2[i]['target'] == this.showIndex){
          let edge = {
            target:this.nodes[this.showIndex],
            source:this.nodes[this.links2[i]['source']]
          }
          if(indexs.indexOf(this.links2[i]['source']) == -1){
            indexs.push(this.links2[i]['source'])
          }
          links.push(edge)
        }
      }
      for(let i = 0;i<this.links1.length;i++){
        if(indexs.indexOf(this.links1[i]['source'])>=1 &&
         indexs.indexOf(this.links1[i]['target'])>=1){
          let edge = {
            source:this.nodes[this.links1[i]['source']],
            target:this.nodes[this.links1[i]['target']]
          }
          
          rela.push(edge)
        }
      }
      
      for(let i = 0;i<this.links2.length;i++){
        if(indexs.indexOf(this.links2[i]['source'])>=1 &&
         indexs.indexOf(this.links2[i]['target'])>=1){
          let edge = {
            source:this.nodes[this.links2[i]['source']],
            target:this.nodes[this.links2[i]['target']]
          }
          
          rela.push(edge)
        }
      }
      
      let nds = []
      for(let i = 0;i<indexs.length;i++){
        nds.push(this.nodes[indexs[i]])
      }
      return [links,nds,rela]
    },
    showName(){
      return this.user[this.diction[this.showID]]['用户名']
    }
    
  },
  methods:{
      hover(){
        let x = event.offsetX>this.mainSvgConfig.width - 150? event.offsetX - 170:event.offsetX
        let y = event.offsetY>this.mainSvgConfig.height - 200? event.offsetY - 220:event.offsetY
        this.hoverFlag = true;
        this.hoverXY = [x,y]
      },
      linksWidth(d){
        if(this.stressedID == ''){
          return 1
        }
        else if(this.stressedID){
          if(d.source.id == this.stressedID || d.target.id == this.stressedID){
            return 2
          }
          else{
            return 1
          }
        }
        
      },
      linksOpacity(d){
        if(this.stressedID == ''){
          return 0.8
        }
        else if(this.stressedID){
          if(d.source.id == this.stressedID || d.target.id == this.stressedID){
            return 0.8
          }
          else{
            return 0.05
          }
        }
        
      },
      linksColor(d){
        if(d.target.id == this.showID || d.source.id == this.showID){
          return 'none'
        }
        else{
          return '#f2b0c4'
        }
      },
      nodesColor(d){
        var judge = (id)=>{
         
          for(let i = 0;i<this.showData[0].length;i++){
            
            if(this.diction[this.showData[0][i].source.id] == this.showIndex && this.diction[this.showData[0][i].target.id] == this.diction[id]){
              return 'followed'
            }
            else if(this.diction[this.showData[0][i].target.id] == this.showIndex && this.diction[this.showData[0][i].source.id] == this.diction[id]){
              return 'follow'
            }
          }
        }
        if(d.id == this.showID){
          return '#fe413e'
        }
        else if(judge(d.id) == 'followed'){
          return '#fadd89'
        }
        else if(judge(d.id) == 'follow'){
          return '#5be5b8'
        }
        else{
          return 'white'
        }
        
      },
      input(){
        if(this.diction[this.inputID]>=0){
          this.showID = this.inputID
          this.$store.commit({
            type : 'submit',
            recomID:this.inputID,
            name:this.showName
          })
        }
      },
      getAngle(point1,point2){
        let sin = this.tri(point1,point2,"sin")
        return Math.asin(sin)
      },
      getPoint(point1,point2){
        
        let r = 10,
        angle = Math.PI/10
        let theta = this.getAngle(point1,point2)
        
        if(point1.x<point2.x){
          theta = Math.PI-theta
        }
        
        let theta1 = theta - angle,
        theta2 = theta + angle,
        sin1 = Math.sin(theta1),
        sin2 = Math.sin(theta2),
        cos1 = Math.cos(theta1),
        cos2 = Math.cos(theta2)
        
        let x  = this.getNewPos(point2,point1,15)[0],
        y = this.getNewPos(point2,point1,15)[1]
        let p1 = [x  + r * cos1,y + r * sin1],
        p2 = [x + r * cos2,y + r * sin2],
        p3 = [x,y]
        return [p1,p2,p3]
        
      },
      tri(point1,point2,type){
        let x1 = point1.x,
        x2 = point2.x,
        y1 = point1.y,
        y2 = point2.y,
        a = y1-y2,
        b = x1-x2,
        c =Math.sqrt( a*a + b*b)
        if(type == 'sin'){
          return a/c
        }
        else if (type == 'cos'){
          return b/c
        }
        else if(type == 'tan'){
          return b==0? 0:a/b
        }
      },
      getNewPos(point1,point2,r){
        
        return [point1.x - r * this.tri(point1,point2,"cos"),point1.y - r * this.tri(point1,point2,"sin")]
        
          
      },
    tick(){

        d3.select("#circlesLeft")
        .selectAll('circle')
        .attr("cx",d=>d.x)
        .attr("cy",d=>d.y)

    },
    log(){
      console.log(this.showData)
      
      /* console.log(this.nodes)
      console.log(this.links1)
      console.log(this.links2) */
    },
    combine(a,b){
      let sol = []
      for(let i = 0;i<a.length;i++){
        sol.push(a[i])
      }
      for(let i = 0;i<b.length;i++){
        sol.push(b[i])
      }
      return sol
    }
  },
  watch:{
    recomID(){
      d3.select("#circlesLeft")
      .selectAll("circle")
      
      .attr("stroke",'red')
      .attr("stroke-width",0)
      
      d3.select("#circlesLeft")
      .selectAll("circle")
      .filter(d=>d.id == this.recomID)
      
      .attr("stroke",'white')
      .attr("stroke-width",3)
      
      
    },
    selectedAl(val){
      this.$store.commit({
        type:'changeAl',
        algo:val
      })
    },
    showID(val){
      console.log(val)
      if(this.diction[val]>=0){
        this.force.links(this.showData[0])
        //this.force.links(this.combine(this.showData[2],this.showData[0]))
        .linkStrength(1)
        .linkDistance(100)
        .nodes(this.showData[1])
        .start();
        
        d3.select("#circlesLeft")
        .selectAll("circle")
        .data(this.showData[1])
        .enter()
        .append('circle')
        .call(this.force.drag)
        
        d3.select("#circlesLeft")
        .selectAll("circle")
        .data(this.showData[1])
        .attr("r",d=>d.id == this.showID? 15:8)
        .attr("fill",d=>this.nodesColor(d))
        .attr("fill-opacity",0.95)
        .on("mouseover",d=>{
          this.stressedID = d.id;
          d3.select("#circlesLeft")
          .selectAll("circle")
          .filter(d=>this.stressedData[0].indexOf(d.id)!=-1)
          .attr("fill-opacity",0.95)
          
          d3.select("#circlesLeft")
          .selectAll("circle")
          .filter(d=>this.stressedData[0].indexOf(d.id)==-1)
          .attr("fill-opacity",0.1)
        })
        .on("mousemove",()=>{
          this.hover()
        })
        .on("mouseout",()=>{
          this.hoverFlag = false
          this.stressedID = ''
          
          d3.select("#circlesLeft")
          .selectAll("circle")
          .attr("fill-opacity",0.95)
          
        })
        .on("click",(d)=>{
          
          this.$store.commit({
            type : 'submit',
            recomID:d.id,
            name:this.user[this.diction[d.id]]['用户名']
          })
        })
        
        d3.select("#circlesLeft")
        .selectAll("circle")
        .data(this.showData[1])
        .exit()
        .remove()
      }
      else{
        d3.select("#circlesLeft")
        .selectAll("circle")
        .remove()
      }
      
      
      
    }
  }
}
</script>

