    margin-left:10%;
    margin-right:1%;
    font-size:14px;
    font-family: sans-serif;
    
  }
  #mainGraph{
    position:absolute;
    left:10px;
    top:35px;
    background-color: black;
    opacity:0.8;
    border-radius: 10px;
  }
  .textIn{
    outline-style: none;
    height:15px;
    width:20%;
    margin-left:1%;
    margin-right:0%;
    margin-bottom:5px;
    border: 2px solid #37AF70;
    border-radius: 5px; 
  }
  .selectIn{
    
    outline-style: none;
    height:20px;
    width:15%;
    
    margin-left:1%;
    margin-right:0%;
    margin-bottom:5px;
    border: 2px solid #37AF70;
    border-radius: 5px;
   
    
  }
  #timeBtn{
    transition: all 0.5s;
    cursor:pointer;
    
    border-radius: 5px;
